
ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
body,
p {
	margin: 0;
	font-size: 14px;
	text-overflow: ellipsis;
	white-space: nowrap;
}
a {
	color: #666666!important;
	text-decoration: none;
}
a:hover {
	color: #666666!important;
	text-decoration: none;
	border: none;
}
.fl {
	float: left;
}
.fr {
	float: right;
}
.root {
	padding-bottom: 30px;
}
.isBlue {
	color: #1A60FF;
}
/*    headernav    */
.hd_nav {
	height: 45px;
	line-height: 45px;
	text-align: center;
	background: #2D343C;
	color: white;
	padding: 0 20px;
	overflow: hidden;
}
.hd_nav .fl {
	float: left;
	font-size: 26px;
	margin-top: 10px;
}
.hd_nav h4 {
	margin-top: 13px;
}
.hd_nav .fr {
	float: right;
	margin-top: -34px;
}
/*进入动画*/
.routein-enter-active {
	z-index: 10;
}
.routein-enter-active,
.routein-leave-active {
	transition: all 0.3s;
	position: absolute;
}
.routein-enter {
	transform: translateX(100%);
}
.routein-leave-active {
	transform: translateX(-50%);
}
/*退出动画*/
.routeout-enter-active,
.routeout-leave-active {
	transition: all 0.3s;
	position: absolute;
}
.routeout-enter {
	transform: translateX(-50%);
}
.routeout-leave-active {
	transform: translateX(100%);
	z-index: 10;
}
.hd {
	pos-bottom: fixed;
	top: 0;
	left: 0;
}
/*   注册登录页面   */
.res_lo {
	padding-top: 75px;
	text-align: center;
	background: #397AD4;
	height: 100vh;
	color: #FFFFFF;
}
.res_lo>h2 {
	font-size: 25px;
	margin: 0;
	padding: 0;
	font-weight: bold;
}
.res_lo>h2+div {
	font-size: 26px;
}
.l-main {
	width: 70%;
	margin: 50px auto;
	text-align: left;
	font-size: 14px;
}
.form-div {
	border-bottom: 1px solid white;
}
.in_wrap {
	width: 95%;
	height: 52px;
	line-height: 74px;
	display: inline-block;
}
.res_lo input {
	border: none;
	background: transparent;
	height: 30px;
}
.res_lo button {
	width: 70%;
	display: block;
	margin: 0 auto;
	height: 35px;
	line-height: 35px;
	border-radius: 20px;
	border: none;
}
.res_lo button[type='submit'] {
	color: #397AD4;
	background: white;
	margin: 45px auto 28px;
}
.res_lo .links {
	color: white;
}
/*     选取按钮    */
.sele_btns {
	padding: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	height: 60px;
	background: #F0F0F0;
	line-height: 60px;
}
.sele_btns div {
	width: 40vw;
	height: 9vw;
	line-height: 9vw;
	text-align: center;
	border-radius: 20px;
	color: white;
	font-size: 14px;
}
.sele_btns span {
	display: inline-block;
	width: 20px;
	height: 20px;
	line-height: 20px;
	border-radius: 50%;
	text-align: center;
	background: white;
	color: #EC543D;
}
.locat_btn {
	background: #295FCC;
}
.his_btn {
	background: #808080;
}
/*     报警   页面    */
.alert_list>li {
	border-bottom: 5px solid #E6E6E6;
	overflow: hidden;
}
.alert_list li>img {
	width: 25%;
	min-height: 90px;
	float: left;
}
.alert_con {
	width: 75%;
	float: left;
	padding: 10px;
}
.alert_con>ul {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	text-align: center;
	margin-bottom: 15px;
}
.alert_con li {
	width: 25vw;
}
.alert_con li>p:first-of-type {
	width: 25px;
	height: 25px;
	border-radius: 50%;
	line-height: 25px;
	text-align: center;
	color: white;
	margin: 5px auto;
}
.alertG {
	background: #01C854;
}
.alertO {
	background: #FFA826;
}
.alertR {
	background: #EA513F;
}
.alert_bot span {
	background: #E6E6E6;
	margin-top: 5px;
	border-radius: 20px;
	padding: 2px 10px;
}
.vux-badge {
	margin-left: 3px;
}
.tit {
	/*height: 40px;*/
	color: #FFFFFF;
	font-size: 15px;
	font-weight: bold;
	padding: 10px 20px;
}
/*详情描述  */
.des_con {
	padding: 5px;
}
.btns {
	height: 40px;
	line-height: 40px;
	padding-left: 20px;
	color: #FFFFFF;
}
.des_ls {
	position: relative;
}
.des_ls li {
	color: #555;
	margin: 15px;
}
.emp {
	height: 5px;
}
/*    添加任务    设备报修     */
.addw_ls li {
	border-bottom: 1px solid #F0F0F0;
	height: 40px;
	line-height: 40px;
	padding: 0 20px;
	overflow: hidden;
	color: #989898;
}
/*.addw_ls li:after {clear:both;content:'';display:block;width: 0;height: 0;visibility:hidden;}*/
.addw_ls input {
	border: none;
}
.addw_ls .cir {
	display: inline-block;
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background: blue;
}
.addw_ls .fr {
	text-align: right;
}
.addw_ls select {
	border: none;
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
}
.sub_btn {
	color: #397AD4;
	display: block;
	width: 80%;
	border-radius: 20px;
	border: none;
	background: #01C854;
	padding: 5px 10px;
	margin: 0 auto;
	color: white;
}
textarea {
	border: none;
}
.addw_ls .other {
	border: 5px solid #F0F0F0;
	height: 120px;
	margin-left: -5px;
}



